@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@600;800&display=swap");
:root {
	/* ### Primary */

	--soft-cyan: hsl(174, 77%, 80%); /* (Full Slider Bar) */
	--strong-cyan: hsl(174, 86%, 45%); /* (Slider Backround) */
	--light-grayish-red: hsl(14, 92%, 95%); /*  (Discount Background) */
	--light-red: hsl(15, 100%, 70%); /* (Discount Text) */
	--pale-blue: hsl(226, 100%, 87%); /*  (CTA Text) */
	/* ### Neutral */

	--white: hsl (0, 0%, 100%); /* (Pricing Component Background) */
	--very-pale-blue: hsl(230, 100%, 99%); /* (Main Background) */
	--lighter-grayish-blue: hsl(224, 65%, 95%); /* (Empty Slider Bar) */
	--light-grayish-blue: hsl(223, 50%, 87%); /* (Toggle Background) */
	--grayish-blue: hsl(225, 20%, 60%); /* (Text) */
	--dark-desaturated-blue: hsl(227, 35%, 25%); /* (Text) */
}

*,
*::after,
*::before {
	margin: 0;
	padding: 0;
	border: 0;
	box-sizing: border-box;
	font-family: "Manrope", sans-serif;
}
body {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100vw;
	height: 100vh;
	background-color: var(--very-pale-blue);
}
#root {
	width: 1440px;
	height: 1000px;
	display: flex;
	justify-content: center;
	align-items: center;
	background: no-repeat url("./images/pattern-circles.svg") 50% 20%,
		no-repeat url("./images/bg-pattern.svg");
	box-shadow: 0 0.6em 0.6em var(--light-grayish-blue);
}
@media (min-width: 1440px) {
	#root {
		height: 800px;
	}
}
